'use client';

import { useEffect, useRef, useState } from 'react';
import { useRouter } from 'next/navigation';
import { services } from '@/services/api';
import type { Empresa } from '@/services/api/empresa.service';
import { cookies } from '@/utils/cookies';

export interface User {
  id: number;
  nome: string;
  email: string;
  role: 'SUPERADMIN' | 'ADMIN' | 'GESTOR' | 'VENDEDOR' | 'CLIENTE';
  empresaId?: number;
  empresa?: Empresa;
}

export function useAuth() {
  const router = useRouter();
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);
  const isMounted = useRef(false);

  useEffect(() => {
    if (isMounted.current) return;
    // Verificar se está no cliente antes de usar localStorage
    if (typeof window === 'undefined') {
      setLoading(false);
      return;
    }
    
    const cachedUser = localStorage.getItem('user');
    if (cachedUser && !user) {
      try {
        setUser(JSON.parse(cachedUser));
      } catch (error) {
        console.error('Erro ao parsear usuário do cache:', error);
        localStorage.removeItem('user');
      }
    }
    isMounted.current = true;
  }, [user]);

  useEffect(() => {
    // Verificar se está no cliente antes de usar localStorage
    if (typeof window === 'undefined') {
      setLoading(false);
      return;
    }

    const ultimoCheck = localStorage.getItem('ultimoCheck');
    const loadingCheck = localStorage.getItem('loadingCheck');
    const now = Date.now();
    const diff = now - parseInt(ultimoCheck || '0');
    const cachedUser = localStorage.getItem('user');
    if ((!cachedUser && !ultimoCheck && !loadingCheck) || (ultimoCheck && diff > 60_000)) {
      // console.log('removendo loadingCheck e ultimoCheck porque diff é maior que 60 segundos');
      localStorage.removeItem('loadingCheck');
      localStorage.removeItem('ultimoCheck');
    }
    const checkAuth = async () => {
      localStorage.setItem('loadingCheck', 'true');
      try {
        const response = await services.auth.getMe();
        console.log('Authenticated user:', response.data);
        setUser(response.data);
        localStorage.setItem('user', JSON.stringify(response.data));
        localStorage.removeItem('loadingCheck');
        localStorage.setItem('ultimoCheck', Date.now().toString());

        await new Promise(resolve => setTimeout(resolve, 10000));
      } catch (error) {
        // Se houver erro, limpa o token e redireciona para login
        // localStorage.removeItem('token');
        cookies.token.remove();
        router.push('/auth/login');
      } finally {
        setLoading(false);
      }
    };

      if (loadingCheck || diff < 10_000) {
        // console.log('bloqueando checkAuth porque loadingCheck é true ou diff é menor que 10 segundos, loadingCheck: ', !!loadingCheck, ', diff: ', diff);
        setLoading(false);
        return;
      }
      checkAuth();
  }, [router]);

  const logout = (redirect: boolean = true) => {
    cookies.auth.clear();
    setUser(null);
    if (typeof window !== 'undefined') {
      localStorage.removeItem('user');
      localStorage.removeItem('ultimoCheck');
      localStorage.removeItem('loadingCheck');
    }
    if (redirect) {
      router.push('/auth/login');
    }
  };

  const updateUser = (newUserData: Partial<User>) => {
    if (user) {
      setUser({ ...user, ...newUserData });
    }
  };

  const isAuthenticated = !!user;

  return {
    user,
    loading,
    isAuthenticated,
    logout,
    updateUser,
  };
}

// Hook para verificar permissões do usuário
export function useAuthorization(allowedRoles: User['role'][]) {
  const { user, loading } = useAuth();
  const router = useRouter();

  useEffect(() => {
    if (!loading && user && !allowedRoles.includes(user.role)) {
      router.push('/unauthorized');
    }
  }, [user, loading, router, allowedRoles]);

  return {
    isAuthorized: !loading && user && allowedRoles.includes(user.role),
    loading,
  };
}
